// SobreNosotros.js
import React, { useEffect, useRef, useState } from 'react';
import '../css/Sobre_Nosotros.css';
import MenuBar from './MenuBar';
import SkipToContent from './component/skipToContent/SkipToContent';
import LiveRegion from './component/skipToContent/LiveRegion';
import { peticionGet, URLBASE } from '../utilities/hooks/Conexion';
import { mensajesSinRecargar } from '../utilities/Mensajes';
import { getToken } from '../utilities/Sessionutil';

const SobreNosotros = () => {
  const mainContentRef = useRef(null);
  const [licencia, setLicencia] = useState(null);
  const[nombreLicencia, setNombreLicencia] = useState("Licencia Inclusin Lectora");
  useEffect(() => {

    peticionGet(getToken(), `licencia`)
      .then((info) => {
        if (info.code === 200) {
          setLicencia(`${URLBASE}${info.info.rutaRelativa}`);
          setNombreLicencia(info.info.nombreArchivo);
        } else {
          mensajesSinRecargar("No se encontr la licencia", 'error', 'Error');
        }
      })
      .catch(() => {
        mensajesSinRecargar("Error al buscar la licencia", 'error', 'Error');
      });

  });
  return (
    <>
      <SkipToContent
        targetId="main-content"
        label="Area de informacion"
      />

      <LiveRegion />
      <header>
        <MenuBar />
      </header>
      <main ref={mainContentRef}
        id="main-content"
        tabIndex="-1"
        aria-labelledby="page-title" className="sobre-nosotros">
        <section className="hero">
          <div className="hero-content">
            <h1 className="titulo-principal">Inclusin Lectora</h1>
            <p className="descripcion">
              Transformamos documentos PDF en archivos de audio MP3 para promover la inclusin de personas con discapacidad visual.
            </p>
          </div>
        </section>

        <div className="main-content">
          <section className=" info">
            <div className="contenedor">
              <h2 className="titulo-secundario">Qu es Inclusin Lectora?</h2>
              <p className="texto-normal">
                Inclusin Lectora es una aplicacin que convierte documentos PDF a formato MP3, ayudando a personas con discapacidad visual a acceder a contenidos de forma prctica e inclusiva. Nuestro objetivo es derribar barreras y garantizar igualdad de acceso a la informacin.
              </p>
            </div>
          </section>

          <section className="equipo">
            <div className="contenedor">
              <h2 className="titulo-secundario">Equipo del Proyecto</h2>
              <ul className="lista-detallada">
                <li><strong>Desarrollador:</strong> Yovin Stiven Urrego Gmez - <a href="mailto:yovin.urrego@unl.edu.ec">yovin.urrego@unl.edu.ec</a></li>
                <li><strong>Docente Involucrado:</strong> Oscar Miguel Cumbicus Pineda - <a href="mailto:oscar.cumbicus@unl.edu.ec">oscar.cumbicus@unl.edu.ec</a></li>
                <li><strong>Docente Involucrado:</strong> Pablo Fernando Ordoez Ordoez - <a href="mailto:fjalvarez@unl.edu.ec">pfordonez@unl.edu.ec</a></li>
                <li><strong>Docente Involucrado:</strong> Hernn Leonardo Torres Carrin - <a href="mailto:fjalvarez@unl.edu.ec">hltorres@unl.edu.ec</a></li>
                <li><strong>Docente Involucrado:</strong> Francisco Javier Alvarez Pineda - <a href="mailto:fjalvarez@unl.edu.ec">fjalvarez@unl.edu.ec</a></li>
              </ul>
            </div>
          </section>
          {licencia && (
            <section className="equipo">
              <div className="contenedor">
                <h2 className="titulo-secundario">Licencia</h2>
                <a
                  href={licencia}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="boton-ver-licencia"
                >
                  Ver Licencia ({nombreLicencia})
                </a>
              </div>
            </section>
          )}
        </div>
      </main>
      <footer className="footer">
        <div className="contenedor">
          <p className="descripcion"> {new Date().getFullYear()} Inclusin Lectora. Todos los derechos reservados.</p>
        </div>
      </footer>

    </>
  );
};

export default SobreNosotros;